import * as mutations from './mutation_types.js'
import moment from 'moment'

export const actions = {
  initBetSlips ({dispatch}) {
    dispatch('getBetSlip');
    dispatch('getGroupBetSlip')
  },
  getBetSlip ({commit}) {
    let betSlip = JSON.parse(window.localStorage.getItem('betSlip'));
    if (betSlip) {
      commit(mutations.SET_BET_SLIP, betSlip)
    }
  },
  getGroupBetSlip ({commit}) {
    let betSlip = JSON.parse(window.localStorage.getItem('betGroupSlip'));
    if (betSlip) {
      commit(mutations.SET_GROUP_BET_SLIP, betSlip)
    }
  },
  updateBetSlip ({commit}, payload) {
    // the store
    let betSlip = [];
    let oldSlip = JSON.parse(window.localStorage.getItem('betSlip'));

    if (oldSlip != null) {
      // Change odd if picked
      oldSlip = oldSlip.filter((i) => {
        return i.gameId !== payload.game.game_id
      });

      // resend the odds to the array
      oldSlip.push({
        game: { home_team: payload.game.home_team, away_team: payload.game.away_team,},
        gameId: payload.game.game_id,
        odd: {
            market_name: payload.odd.market_name,
            market_handicap: payload.odd.handicap
        },
        oddId: payload.odd.id,
        oddOutCome: payload.odd.outcome,
        oddValue: payload.odd.value,
        market: payload.odd.market_id,
        scheduled: moment.utc(payload.game.scheduled).local().format()
      });
      commit(mutations.SET_BET_SLIP, oldSlip);
      window.localStorage.setItem('betSlip', JSON.stringify(oldSlip))
    } else {
      // Create new bet slip
      betSlip.push({
        game: { home_team: payload.game.home_team, away_team: payload.game.away_team,},
        gameId: payload.game.game_id,
        odd: {
          market_name: payload.odd.market_name,
          market_handicap: parseFloat(payload.odd.handicap)
        },
        oddId: payload.odd.id,
        oddOutCome: payload.odd.outcome,
        oddValue: payload.odd.value,
        market: payload.odd.market_id,
        scheduled: moment.utc(payload.game.scheduled).local().format()
      });
      // Add to the local storage.
      commit(mutations.SET_BET_SLIP, betSlip);
      window.localStorage.setItem('betSlip', JSON.stringify(betSlip))
    }
  },

  removeFromSlip ({commit}, payload) {
    let gameId = null;
    if (payload.game_id) {
        gameId = payload.game_id
    } else {
        gameId = payload.gameId
    }
    let oldSlip = JSON.parse(window.localStorage.getItem('betSlip'));
    if (gameId) {
      oldSlip = oldSlip.filter((i) => {
        return i.gameId !== gameId
      })
    } else if (payload.game) {
      oldSlip = oldSlip.filter((i) => {
        return i.gameId !== payload.game.game_id
      })
    }
    commit(mutations.SET_BET_SLIP, oldSlip);
    window.localStorage.setItem('betSlip', JSON.stringify(oldSlip))
  },
  clearBetSlip ({commit}) {
    commit(mutations.UNSET_BET_SLIP)
  }
};
