import * as mutationTypes from './mutation_types'
import {GAME_MARKETS_URL} from "../../../api_routes";
import axios from 'axios';

export const actions = {
  getGameMarkets ({commit, dispatch}, payload) {
    // Check local storage
    let storedGameMarkets = JSON.parse(window.localStorage.getItem('gameMarkets'));
    if (storedGameMarkets) {
      window.localStorage.removeItem('gameMarkets');
      // Set the incoming game to game markets
      window.localStorage.setItem('gameMarkets', JSON.stringify(payload));

      dispatch('getMoreMarkets', payload);

      // Commit the mutation
      commit(mutationTypes.SET_GAME_MARKETS, JSON.parse(window.localStorage.getItem('gameMarkets')))
    } else {
      window.localStorage.setItem('gameMarkets', JSON.stringify(payload));
      // Commit the mutation
      commit(mutationTypes.SET_GAME_MARKETS, JSON.parse(window.localStorage.getItem('gameMarkets')));
      dispatch('getMoreMarkets', payload);
    }
  },

  getMoreMarkets ({commit}, payload) {
      axios.get(GAME_MARKETS_URL + payload.id).then(response => {
        payload.markets = response.data.markets;
        if(response.status === 200) commit(mutationTypes.SET_GAME_MARKETS, payload);
      });
  }
};
