export const getters = {
  soccerCategories: state => {
    if (state.allData.length !== 0) {
      return state.allData[0].categories
    }
  },
  basketballCategories: state => {
    if (state.allData.length !== 0) {
      return state.allData[1].categories
    }
  },
  rugbyCategories: state => {
    if (state.allData.length !== 0) {
      return state.allData[2].categories
    }
  },

  allGames: state => {
    return state.allData
  }
}
