import * as userMutations from './mutation_types'
import {
    CUSTOMER_BET_HISTORY_URL,
    TRANSACTION_URL,
    AUTH_USER_URL,
    IP_STACK,
    CLIENT_IP_VISIT,
    USER_DETAIL_URL, INVITED_MAN_URL, REQUESTS_MAN_URL, LOGIN_URL
} from '../../../api_routes'
import {AUTH_CONFIG} from '../../../auth_config'
import axios from 'axios'

export const actions = {

  logout ({commit}) {
    // Clear the local Storage
    window.localStorage.removeItem('authUser')
    window.localStorage.removeItem('token')
    window.localStorage.clear()
    // TODO: Send request to the api to clear the token from the database
    // Commit the mutation
    commit(userMutations.CLEAR_AUTH_USER)
  },

  loginUser ({dispatch}, payload) {
    return new Promise((resolve, reject) => {
        axios.post(LOGIN_URL, payload).then((response) => {
          resolve(response);
            let loginResponseData = response.data
            if (loginResponseData.code === 200) {
                let token = loginResponseData.token
                window.localStorage.setItem('token', JSON.stringify(token))
                dispatch('init_auth_user', {request: 'refresh'})
            }
        }, (error) => {
            reject(error)
        });
    })

  },

  // Gets the authenticated user, returns it if its in the local storage, get it from the api is not.
  init_auth_user ({commit, dispatch}, payload = null) {
    if (!payload) {
      let authUser = JSON.parse(window.localStorage.getItem('authUser'))
      if (authUser) { commit(userMutations.SET_AUTH_USER, JSON.parse(window.localStorage.getItem('authUser'))) }
      dispatch('getUserDetails')
    } else {
      // Check local storage
      let token = JSON.parse(window.localStorage.getItem('token'))
      if (!token) { return }
      return new Promise((resolve, reject) => {
        axios.get(AUTH_USER_URL, {headers: AUTH_CONFIG.getHeaders()}).then(resp => {
          if (resp.status === 200) {
            resolve(resp)
            // Store the user data in the local storage.
            let authUser = resp.data.data
            if (authUser) {
              authUser.account = authUser.account.data
              authUser.county = authUser.county.data
              authUser.group = authUser.group ? authUser.group.data : null
              authUser.settlements = authUser.settlements.data
              authUser.payments = authUser.payments.data
            }
            // set the client location and ip
            axios.get(IP_STACK).then(response => {
              if (response.data.ip != null) {
                let postData = {
                  ip: response.data.ip,
                  location: response.data.location.capital,
                  user_uuid: authUser.uuid
                }
                axios.post(CLIENT_IP_VISIT, postData, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
                  console.log(response)
                })
              }
            })
            window.localStorage.setItem('authUser', JSON.stringify(authUser))
            commit(userMutations.SET_AUTH_USER, authUser)
            dispatch('getUserTransactions', {userId: authUser.account.owner_id})
            dispatch('getBetHistory', {userId: authUser.account.owner_id})
            dispatch('getUserDetails')
            dispatch('getUserInvites')
            dispatch('getUserManToManRequests')
          } else {
            reject(resp)
          }
        }, (error) => {
          reject(error)
          console.log('UNABLE TO AUTHENTICATE USER')
        })
      })
    }
  },

  getUserDetails ({commit}) {
      // Check local storage
      let token = JSON.parse(window.localStorage.getItem('token'))
      if (!token) { return }
    return new Promise((resolve, reject) => {
      axios.get(USER_DETAIL_URL, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
        if (resp.status === 200) {
          let responseData = resp.data.data
          localStorage.setItem('userDetails', JSON.stringify(responseData))
          commit(userMutations.SET_USER_DETAILS, responseData)
          commit(userMutations.SET_USER_GROUPS)
          resolve(resp)
        }
      }, (error) => {
        reject(error)
      })
    })
  },
  getUserTransactions ({commit}) {
    let transactions = JSON.parse(window.localStorage.getItem('user_transactions'))
    if (transactions !== null) {
      window.localStorage.removeItem('user_transactions')
    }
    return new Promise((resolve, reject) => {
      axios.get(TRANSACTION_URL, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
        if (response.status === 200) {
          window.localStorage.setItem('user_transactions', JSON.stringify(response.data))
          // Commit the mutation
          commit(userMutations.SET_USER_TRANSACTIONS, response.data)
          resolve(response)
        } else {
          reject(response)
        }
      }, (error) => {
        reject(error)
      })
    })
  },

  getBetHistory ({commit}) {
    let transactions = JSON.parse(window.localStorage.getItem('betHistory'))
    if (transactions !== null) {
      window.localStorage.removeItem('betHistory')
    }
    return new Promise((resolve, reject) => {
      axios.get(CUSTOMER_BET_HISTORY_URL, {headers: AUTH_CONFIG.getHeaders()}).then((response) => {
        if (response.status === 200) {
          // Send the transactions to the local storage
          let userHistory = response.data.data
          window.localStorage.setItem('betHistory', JSON.stringify(userHistory))
          // Commit the mutation
          commit(userMutations.SET_USER_BET_HISTORY, userHistory)
          resolve(response)
        } else {
          reject(response)
        }
      }, (error) => {
        reject(error)
      })
    })
  },
  getUserInvites ({commit}) {
    axios.get(INVITED_MAN_URL, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
      let invitations = response.data.data
      commit(userMutations.SET_USER_INVITATIONS, invitations)
    })
  },
  getUserManToManRequests ({commit}) {
    axios.get(REQUESTS_MAN_URL, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
      let requests = response.data.data
      commit(userMutations.SET_USER_MAN_TO_MAN_REQUESTS, requests)
    })
  }
}
