import * as mutationTypes from './mutation_types'
import axios from 'axios'
import {JACKPOT_URL} from '../../../api_routes'
import moment from 'moment';
import {GAME} from "../../../games";

export const actions = {
  getJackpotGames ({commit}) {
    // Check the local storage or the data
    if (window.localStorage.getItem('jackpotGames') != null) {
      commit(mutationTypes.SET_JACKPOT_GAMES, JSON.parse(window.localStorage.getItem('jackpotGames')))
    } else {
      return new Promise((resolve, reject) => {
        axios.get(JACKPOT_URL).then((response) => {
          if (response.status === 200) {
            resolve(response);
            // Update the local storage
              let apiResponse = response.data;

              let games = GAME.filterStartedGames(apiResponse);
              if (games) window.localStorage.setItem('jackpotGames', JSON.stringify(apiResponse));

            commit(mutationTypes.SET_JACKPOT_GAMES, games);
          }
        }, (error) => {
          reject(error);
        })
      })
    }
  },

    getJackpotSlip ({commit}) {
        let betSlip = JSON.parse(window.localStorage.getItem('jackpotSlip'));
        if (betSlip) {
            commit(mutationTypes.SET_JACKPOT_SLIP, betSlip)
        }
    },

    addToJackpotSlip ({commit}, payload) {
      let game = payload.game;
      let odd = payload.odd;
        let selectedGame = {
            game: { home_team: game.home_team, away_team: game.away_team,},
            gameId: game.game_id,
            odd: {
                market_name: odd ? odd.market_name : '',
                market_handicap: odd ? odd.handicap : ''
            },
            oddId: odd.id,
            oddOutCome: odd.outcome,
            oddValue: odd.value,
            market: odd.market_id,
            scheduled: moment.utc(game.scheduled).local().format(),
            count: 1
        };

        let jackpotSlip = JSON.parse(localStorage.getItem('jackpotSlip'));

        let newJackpotSlip = [];

        if (jackpotSlip) {
          jackpotSlip.forEach(item => {
            if (item.gameId == game.game_id) {
              if (item.count < 3){
                item.count += 1;
              }
            }
            if (item.count > 2) {
              return;
            } else{
              jackpotSlip.push(selectedGame);
            }
          });
            // Update the local storage;
            localStorage.setItem('jackpotSlip', JSON.stringify(jackpotSlip));
            commit(mutationTypes.SET_JACKPOT_SLIP, jackpotSlip);
        } else {
          newJackpotSlip.push(selectedGame);
          localStorage.setItem('jackpotSlip', JSON.stringify(newJackpotSlip));
          commit(mutationTypes.SET_JACKPOT_SLIP, newJackpotSlip);
        }

    },

    clearJackpotSlip ({commit}) {
      let slip = JSON.parse(localStorage.getItem('jackpotSlip'));
      if (slip) localStorage.removeItem('jackpotSlip');
      commit(mutationTypes.CLEAR_JACKPOT_SLIP);
    }
};
