import moment from 'moment'

export const GAME = {
    GAME_TYPE : {
        SOCCER: 10,
        BASKETBALL: 4,
        RUGBY: 73744,
    },

    getTomorrowsGames (gamesArray) {
        let tomorrowsGames;
        tomorrowsGames = [];
        let startOfTomorrow = moment().add(1, 'day').startOf('day').format('YYYY-MM-DD HH:mm:ss');
        let endOfTomorrow = moment().add(1, 'day').endOf('day').format('YYYY-MM-DD HH:mm:ss');
        if (!gamesArray) return;
        tomorrowsGames = gamesArray.filter(game => {
            let gameTime = moment.utc(game.scheduled).local().format('YYYY-MM-DD HH:mm:ss');
            if (gameTime > startOfTomorrow && gameTime < endOfTomorrow)
                return game
        });
        return tomorrowsGames
    },
  isStarted (game) {
    if (game) {
      // Add 12 seconds to the current time.
      let currentTime = moment().format('YYYY-MM-DD HH:mm:ss');
      let gameTime = moment.utc(game.scheduled).local().format('YYYY-MM-DD HH:mm:ss');
      let isStarted = false;
      if (gameTime < currentTime) {
        isStarted = true
      }
      return isStarted
    }
  },

  groupGameIsStarted (game) {
    if (game) {
      // Add 12 seconds to the current time.
      let currentTime = moment().format('YYYY-MM-DD HH:mm:ss');
      let gameTime = moment(game.scheduled).format('YYYY-MM-DD HH:mm:ss');
      let isStarted = false;
      if (gameTime < currentTime) {
        isStarted = true
      }
      return isStarted
    }
  },

  filterStartedGames (GamesArray) {
    let unStartedGames = null;
    if (GamesArray.length) {
      unStartedGames = GamesArray.filter((game) => {
        return !this.isStarted(game)
      })
    }
    return unStartedGames
  },
    fullTime (scheduled) {
        return moment(scheduled).format('HH:mm')
    },
    dateName (scheduled) {
        return moment.utc(scheduled).local().format('ddd, MMM Do')
    },
    displayTime (scheduled) {
        return moment.utc(scheduled).local().format('HH:mm')
    },
    mobileDate (scheduled) {
        return moment.utc(scheduled).local().format('ddd')
    },
    hasOdds (game) {
        let isOdds = false;
        game.forEach((odds) => {
            if (odds.value >= 1.01) {
                isOdds = true
            }
        });
        return isOdds
    },
};
