export const AUTH_CONFIG = {
    CLIENT_ID: 2,
    CLIENT_SECRET: 's7cDkCX0BqXmHsxHwGPjdPGifHkK5tbnO1PUHsAc',
    GRANT_TYPE: 'password',
    getHeaders: () => {
        let headers = null
        let tokenData = null
        try {
            tokenData = JSON.parse(window.localStorage.getItem('token'))
        } catch (exception) {
          return
        }
        if (tokenData) {
            headers = {
                'Accept': 'application/json',
                'Authorization': 'Bearer ' + tokenData
            }
            return headers
        } else {
            return null
        }
    },

    detectUserBrowser(userAgent) {
        let opera = Object.prototype.toString.call(window.operamini) === "[object OperaMini]"
        if (opera)
            return 'opera'
        else
            switch (true) {

                case userAgent.indexOf('Opera Mini') > -1 && !!window.opr :
                    return 'opera';
                case userAgent.indexOf('edge') > -1 :
                    return 'edge';
                case userAgent.indexOf('chrome') > -1 && !!window.chrome :
                    return 'edge'
                case userAgent.indexOf('ie') > -1 :
                    return 'ie'
                case userAgent.indexOf('firefox') > -1 :
                    return 'firefox'
                case userAgent.indexOf('safari') > -1 :
                    return 'safari'
                default :
                    return 'other'
            }
    }
}
