import * as mutationTypes from './mutation_types'
import axios from 'axios'
import {FEATURED_GAMES_URL} from '../../../api_routes'
import {GAME} from '../../../games'

export const actions = {
  getFeaturedGames ({commit}) {
    // Check the local storage first
    let featuredGames = JSON.parse(window.localStorage.getItem('featuredGames'))
    if (featuredGames) { featuredGames = GAME.filterStartedGames(featuredGames) }
    if (featuredGames != null) {
      commit(mutationTypes.SET_FEATURED_GAMES, featuredGames)
    } else {
      return new Promise((resolve, reject) => {
        axios.get(FEATURED_GAMES_URL).then((response) => {
          if (response.status === 200) {
            let responseData = response.data

            // Set the items in the local storage
            let featuredGames = responseData.data

            // Filter for started games.
            if (featuredGames) {
              featuredGames = GAME.filterStartedGames(featuredGames)
            }
            if (featuredGames) {
              window.localStorage.setItem('featuredGames', JSON.stringify(featuredGames))
              // Commit the mutation.
              commit(mutationTypes.SET_FEATURED_GAMES, featuredGames)
            }

            resolve(response.data)
          }
        }, (error) => {
          // console.log('UNABLE TO LOAD JACKPOTS', error.response.data)
          reject(error)
        })
      })
    }
  },

  refreshFeaturedGames ({commit, dispatch}) {
    let featuredGames = JSON.parse(window.localStorage.getItem('featuredGames'))
    if (featuredGames) {
      // Remove it from the local storage
      window.localStorage.removeItem('featuredGames')
    }
    dispatch('getFeaturedGames')
  }

}
