import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// Use lazy loading to load all the components
const HomeComponent = () => import('@/components/Pages/Home.vue')
const LoginComponent = () => import('@/components/Users/MobileLogin.vue')
const RegisterComponent = () => import('@/components/Users/Register.vue')
const ProfileComponent = () => import('@/components/Users/Profile.vue')
const ForgotPasswordComponent = () => import('@/components/Users/ForgotPassword.vue')
const ResetPasswordComponent = () => import('@/components/Users/ResetPassword.vue')
const VerifyUser  = () => import('./../components/Users/VerifyUser')


// Transactions Components
const TransactionsComponent = () => import('./../components/Transactions/Transactions.vue')
const DepositComponent = () => import('./../components/Transactions/Deposit.vue')
const BetHistoryComponent = () => import('./../components/Transactions/BetHistory.vue')
const WithdrawComponent = () => import('./../components/Transactions/Withdraw.vue')
const SambazaStakeComponent = () => import('./../components/Transactions/SambazaStake.vue')
const BetDetailComponent = () => import('./../components/Transactions/BetDetail.vue')
const AccountBalanceComponent = () => import('./../components/Transactions/AccountBalanceComponent.vue')

// Games
const LeagueGames = () => import('../components/Games/LeagueGames.vue')
const SearchComponent = () => import('../components/Games/SearchResults.vue')

// all Components
const MoreSoccerMarketsComponent = () => import('../components/Games/MoreMarkets.vue')

const EditProfile = () => import('../components/Users/EditProfile.vue')

// Pages
const TermsComponent = () => import('./../components/Pages/Terms.vue')
const PlayingComponent = () => import('./../components/Pages/Playing.vue')
const NewsComponent = () => import('./../components/Pages/News.vue')
const OperaComponent = () => import('./../components/Pages/OperaComponent.vue')
const StatisticsComponent = () => import('./../components/Statistics/Statistics.vue')
const LivebetComponent = () => import('./../components/Livebet/Livebet.vue')
const JackpotComponent = () => import('./../components/Jackpot/Jackpot.vue')
const ManBetting = () => import('../components/Alphabet/AlphaBet.vue')
const Test = () => import('./../components/Groups/Test.vue')
const Dev = () => import('./../components/layout/Dev.vue')

// Define all the routes here:
const routes = [
  {
    name: 'home',
    path: '/',
    component: HomeComponent
  },

  // Pages:
  {
    name: 'terms',
    path: '/terms-and-conditions',
    component: TermsComponent
  },
  {
    name: 'playing',
    path: '/how-to-play',
    component: PlayingComponent
  },
  {
    name: 'news',
    path: '/news',
    component: NewsComponent
  },
    {
        name: 'opera',
        path: '/opera',
        component: OperaComponent
    },
  {
    name: 'statistics',
    path: '/statistics',
    component: StatisticsComponent
  },
  {
    name: 'livebet',
    path: '/live-betting',
    component: LivebetComponent
  },

  {
    name: 'jackpot',
    path: '/jackpot',
    component: JackpotComponent
  },
  // End pages

  {
    name: 'accounts',
    path: '/accounts',
    component: AccountBalanceComponent
  },
  // Auth routes
  {
    name: 'login',
    path: '/login',
    component: LoginComponent,
    meta: {

    }
  },
  {
    name: 'register',
    path: '/register',
    component: RegisterComponent,
    meta: {

    }
  },
  {
    name: 'forgot_password',
    path: '/auth/forgot-password',
    component: ForgotPasswordComponent,
    meta: {}
  },
  {
    name: 'reset_password',
    path: '/auth/reset-password',
    component: ResetPasswordComponent,
    meta: {
    }
  },
  {
    name: 'verify-user',
    path: '/auth/verify-user',
    component: VerifyUser,
    meta: {
    }
  },
  {
    name: 'profile',
    path: '/auth/profile',
    component: ProfileComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'edit_profile',
    path: '/auth/edit_profile',
    component: EditProfile,
    meta: {
      requiresAuth: true
    }
  },
  // Transaction Routes
  {
    name: 'transactions',
    path: '/transactions',
    component: TransactionsComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'sambaza-stake',
    path: '/sambaza-stake',
    component: SambazaStakeComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'more-markets',
    path: '/more-markets',
    component: MoreSoccerMarketsComponent,
    props: true
  },
  {
    name: 'deposit',
    path: '/transactions/deposit',
    component: DepositComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'withdraw',
    path: '/transactions/withdraw',
    component: WithdrawComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'bet_history',
    path: '/transactions/bet-history',
    component: BetHistoryComponent,
    meta: {
      requiresAuth: true,
      showAccountSidebar: true,
    }
  },
  {
    name: 'bet-detail',
    path: '/transactions/bet-detail',
    component: BetDetailComponent,
    meta: {
      requiresAuth: true
    }
  },
  {
    // get leagues
    name: 'leagues',
    path: '/leagues/:league_id',
    props: true,
    component: LeagueGames
  },
  {
    // get leagues
    name: 'search',
    path: 'search',
    component: SearchComponent
  },
  {
    name: 'man-betting',
    path: '/man-to-man',
    component: ManBetting,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'test',
    path: '/test',
    component: Test,
    meta: {
      requiresAuth: true
    }
  },
  {
    name: 'dev',
    path: '/dev',
    component: Dev,
    meta: {
      requiresAuth: true
    }
  }

]


export default new VueRouter({
  routes,
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return to.name === 'leagues' ? { x: 0, y: 0 } : savedPosition
  }
})