export const markets = {
  markets: {
    MATCH_RESULT: 2,
    DOUBLE_CHANCE: 7202,
    OVER_UNDER: 259
  },

  MARKET_TYPE: {
    MATCH_RESULT: 'Match Result',
    OVER_UNDER: 'Over/Under',
    DOUBLE_CHANCE: 'Double Chance'
  },

  getOddsForMarket (game, marketName, over_under_handicap = null) {
      let odds = [];
      if (!game || !game.markets) return;


      const markets = game.markets.filter((market) => {
        let correct = true;
        if (market.name === marketName) {
            if (over_under_handicap && parseFloat(market.handicap) !== parseFloat(over_under_handicap)) {
                correct = false;
            }
        } else {
            correct = false;
        }
        return correct;
      });

      markets.forEach((market) => {
        if (market.name === marketName) odds = market.odds
      });

      return odds

  }
};
