import * as mutationTypes from './mutation_types'
import axios from 'axios'
import {UPCOMING_GAMES_URL} from '../../../api_routes'
import {GAME} from '../../../games'
// import moment from 'moment'

export const actions = {
  getUpcomingGames ({commit}) {
    let allUpcomingGames = JSON.parse(window.localStorage.getItem('upcomingGames'))
    if (allUpcomingGames) { allUpcomingGames = GAME.filterStartedGames(allUpcomingGames) }
    if (allUpcomingGames) {
      commit(mutationTypes.SET_UPCOMING_GAMES, allUpcomingGames)
    } else {
      return new Promise((resolve, reject) => {
        axios.get(UPCOMING_GAMES_URL).then((response) => {
          if (response.status === 200) {
            resolve(response)
            let upcomingGames = response.data
            if (upcomingGames) {
              commit(mutationTypes.SET_UPCOMING_GAMES, upcomingGames)
            }
          }
        }, (error) => {
          reject(error)
        })
      })
    }
  },

  loadMoreUpcomingGames ({commit}, paylaod) {
    return new Promise((resolve) => {
      axios.get(UPCOMING_GAMES_URL + '?page=' + paylaod).then(resp => {
        let upcomingGames = resp.data
        resolve(resp);
        commit(mutationTypes.SET_UPCOMING_GAMES, upcomingGames)
      });
    });
  },

  refreshUpcomingGames ({commit, dispatch}) {
    let upcomingGames = JSON.parse(window.localStorage.getItem('upcomingGames'))
    if (upcomingGames) {
      // Remove it from the local storage
      window.localStorage.removeItem('upcomingGames')
    }
    dispatch('getUpcomingGames')
  }
}
