import * as mutationTypes from './mutation_types'

export const mutations = {
  [mutationTypes.SET_BET_SLIP] (state, payload) {
    state.betSlip = payload
  },
  [mutationTypes.SET_GROUP_BET_SLIP] (state, payload) {
    state.groupBetSlip = payload
  },
  [mutationTypes.UNSET_GROUP_BET_SLIP] (state) {
    state.groupBetSlip = []
    let groupBetSlip = window.localStorage.getItem('groupBetSlip')
    if (groupBetSlip) { window.localStorage.removeItem('groupBetSlip') }
  },
  [mutationTypes.UNSET_BET_SLIP] (state) {
    state.betSlip = []
    let betSlip = window.localStorage.getItem('betSlip')
    if (betSlip) { window.localStorage.removeItem('betSlip') }
  }
}
