import * as countyMutations from './mutation_types'
import axios from 'axios'
import {COUNTIES_URL} from '../../../api_routes'

export const actions = {
  getAllCounties ({commit}) {
    return new Promise((resolve, reject) => {
      axios.get(COUNTIES_URL).then((response) => {
        if (response.status === 200) {
          let allCounties = response.data.data
          console.log('PULLING ALL COUNTIES', allCounties)
          commit(countyMutations.SET_ALL_COUNTIES, allCounties)
          resolve(response.data)
        }
      }, (error) => {
        console.log('UNABLE TO LOAD COUNTIES', error.response.data)
        reject(error)
      })
    })
  }
}
