import 'bootstrap'
import './filters/index.js'
import './../public/flags/blank.gif'
import '@fortawesome/fontawesome-free/js/fontawesome.min.js'
import storage from 'local-storage-fallback'

// Or shim window.localStorage
if (!('localStorage' in window)) {
  window.localStorage = storage;
}


import sortBy from 'lodash.sortby'
import map from 'lodash.map'
import VueIdle from 'idle-vue'
import VueNoty from 'vuejs-noty'
import BootstrapVue from 'bootstrap-vue'
import VueAnalytics from 'vue-analytics'

import Vue from 'vue'
import store from './store'
import router  from './router'
import {markets} from './markets'
import {GAME} from './games'
import './registerServiceWorker'
import BackToTop from 'vue-backtotop'
 
Vue.use(BackToTop);

Object.defineProperty(Vue.prototype, '$map', { value: map });
Object.defineProperty(Vue.prototype, '$sortBy', {value: sortBy});
let EventBus = new Vue();
Vue.component('pagination', require('laravel-vue-pagination'));

Vue.use(VueIdle, {
  eventEmitter: EventBus,
  idleTime: 300000
});

Vue.use(VueNoty, {
  timeout: 30000,
  progressBar: false,
  layout: 'topLeft'
});

Vue.use(BootstrapVue);

Object.defineProperty(Vue.prototype, '$marketHelpers', {value: markets});
Object.defineProperty(Vue.prototype, '$gameHelper', {value: GAME});
Object.defineProperty(Vue.prototype, '$EventBus', {value: new Vue()});

Vue.config.productionTip = false;

Vue.filter('truncate', function (string, value) {
  if (string.length > value) {
    return string.substring(0, value) + '...'
  } else {
    return string
  }
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const authUser = JSON.parse(window.localStorage.getItem('authUser'));
    const token = JSON.parse(window.localStorage.getItem('token'));

    if (authUser && token) {
      next()
    } else {
      next({
        name: 'home'
      })
    }
  } else {
    next()
  }
});
Vue.use(VueAnalytics, {
  id: 'UA-123773573-1',
  router,
  autoTracking: {
    pageviewTemplate (route) {
      return {
        page: route.path,
        title: document.title,
        location: window.location.href
      }
    }
  }
});
import App from './App.vue'


const app = new Vue({
  router,
  store,
  render: h => h(App),
  onActive () {
    this.$store.dispatch('refreshUpcomingGames');
    this.$store.dispatch('refreshHighlightedGames');
    this.$store.dispatch('refreshFeaturedGames');
    const authUser = JSON.parse(window.localStorage.getItem('authUser'));
    if (authUser) {
      this.$store.dispatch('init_auth_user', {request: 'refresh'})
    }
  },
  onIdle () {
    this.$store.dispatch('refreshUpcomingGames');
    this.$store.dispatch('refreshHighlightedGames');
    this.$store.dispatch('refreshFeaturedGames');
    const authUser = JSON.parse(window.localStorage.getItem('authUser'));
    if (authUser) {
      this.$store.dispatch('init_auth_user', {request: 'refresh'})
    }
  }
});
app.$mount('#app');