export const getters = {
  authUser (state) { return state.authUser },
  userDetails (state) { return state.userDetails },
  userTransactions (state) {
    if (state.userTransactions) {
      return state.userTransactions
    } else {
      return JSON.parse(window.localStorage.getItem('user_transactions'))
    }
  },
  userGroups (state) {
    return state.userGroups
  },
  betHistory (state) {
    if (state.betHistory) {
      return state.betHistory
    } else {
      return JSON.parse(window.localStorage.getItem('betHistory'))
    }
  },
  users_man_to_man_requests (state) {
    return state.users_man_to_man_requests
  },
  invitations (state) {
    return state.invitations
  }
}
