import * as mutationTypes from './mutation_types'
import axios from 'axios'
import {API_HIGHLIGHTS_URL, SEARCH_GAME_URL} from '../../../api_routes'
import {GAME} from '../../../games'

export const actions = {

  searchGames ({commit}, payload) {
    return new Promise((resolve, reject) => {
      axios.post(SEARCH_GAME_URL, payload).then((response) => {
        commit(mutationTypes.SEARCHED_GAMES, response.data)
        resolve(response)
      }, (error) => {
        reject(error)
      })
    })
  },

  getHighlightedGames ({commit}) {
    // Check the local storage
    let highlightedGames = JSON.parse(window.localStorage.getItem('highlightedGames'))
    if (highlightedGames) { highlightedGames = GAME.filterStartedGames(highlightedGames) }
    if (highlightedGames != null) {
      commit(mutationTypes.SET_HIGHLIGHTED_GAMES, highlightedGames)
    } else {
      return new Promise((resolve, reject) => {
        axios.get(API_HIGHLIGHTS_URL).then((response) => {
          if (response.status === 200) {
          let highlightedGames = response.data.data
            
            // update the local storage
            if (highlightedGames) {
              highlightedGames = GAME.filterStartedGames(highlightedGames)
            }
            
            if (highlightedGames) {
              window.localStorage.setItem('highlightedGames', JSON.stringify(highlightedGames))
              commit(mutationTypes.SET_HIGHLIGHTED_GAMES, highlightedGames)
            }
            resolve(response)
          }
        }, (error) => {
          reject(error)
        })
      })
    }
  },
  refreshHighlightedGames ({dispatch}) {
    let highlightedGames = JSON.parse(window.localStorage.getItem('highlightedGames'))
    if (highlightedGames) {
      // Remove it from the local storage
      window.localStorage.removeItem('highlightedGames')
    }
    dispatch('getHighlightedGames')
  }

}
