import * as mutationTypes from './mutation_types'
import axios from 'axios'
import {SPORTS_URL} from '../../../api_routes'

export const actions = {

  getAllSports ({commit, dispatch}) {
    let allGames = JSON.parse(window.localStorage.getItem('side_menu'));
    if (allGames) {
      commit(mutationTypes.SET_ALL_SPORTS, allGames)
    } else {
      return new Promise((resolve, reject) => {
        axios.get(SPORTS_URL).then((response) => {
          if (response.status === 200) {
            let responseData = response.data.data;
            resolve(response);
            // Set the data to the local storage.
            window.localStorage.setItem('side_menu', JSON.stringify(responseData));
            dispatch('clearAllSports');
            commit(mutationTypes.SET_ALL_SPORTS, responseData)
          }
        }, (error) => reject(error))
      })
    }
  },
  clearAllSports ({commit}) {
    commit(mutationTypes.CLEAR_ALL_SPORTS)
  },

  refreshSideMenu ({dispatch}) {
      let sideMenu = JSON.parse(window.localStorage.getItem('side_menu'));
      if (sideMenu) {
          // Remove it from the local storage
          window.localStorage.removeItem('side_menu')
      }
      dispatch('getAllSports')
  }
};
