import Vue from 'vue'
import moment from 'moment'

Vue.filter('formatDate', function (value) {
  if (value) {
    return moment(String(value)).format('DD-MM-YYYY')
  }
})

Vue.filter('formatTime', function (value) {
  if (value) {
    return moment(String(value)).format('HH:mm')
  }
})
Vue.filter('formatUTCLocalTime', function (value) {
    if (value) {
        return moment.utc(String(value)).local().format('HH:mm')
    }
})
Vue.filter('betDate', function (value) {
  if (value) {
    return moment(String(value)).format('Do MMM')
  }
})

Vue.filter('clearMMMDDYYYYinMS', function (value) {
  if (value) {
    return moment(value).hour(0).minute(0).second(0).valueOf()
  }
})

Vue.filter('truncate', function (string, value) {
  if (string.length > value) {
    return string.substring(0, value) + '...'
  } else {
    return string
  }
})
Vue.filter('truncate_id', function (string) {
  return string.toString().substr(string.length - 5)
})

Vue.filter('myodds', function (string, value) {
  if (string.length > value) {
    return string.substring(0, value)
  } else {
    return string
  }
})
