<template>
  <div id="app">
    <div v-if="!isOpera">
    <top-nav></top-nav>
        <!-- <JackpotAdvert></JackpotAdvert> -->
    <div class="page-main">
        <div class="row">
            <div class="col-md-3 pt-0 pt-sm-3 pt-xl-4 d-print-none" v-if="$route.name === 'test'">
                <Groupbar></Groupbar>
            </div>
            <div class="col-md-3 pt-0 pt-sm-3 pt-xl-4 d-print-none" v-else-if="$route.meta.requiresAuth">
                <AccountBar></AccountBar>
            </div>
            <div class="col-md-3 pt-0 pt-sm-3 pt-xl-4 d-print-none" v-else>
                <Sidebar></Sidebar>
                <!-- <Devbar></Devbar> -->
            </div>
            <div class="col-md-9 pt-0 pt-sm-3 pt-xl-4 col-print-12 pt-print-0">
                <router-view class="animated fadeIn"></router-view>
            </div>
        </div>
    </div>

    <div class="bet-slip-container">
        <Betslip></Betslip>
    </div>
    <back-to-top text="">
        <BetButton type="button" class="btn btn-info btn-to-top">
            <i class="fa fa-chevron-up"></i>
        </BetButton>
    </back-to-top>
    <Footer class="d-print-none"></Footer>
    </div>
      <div v-else>
          <opera-component></opera-component>
      </div>
</div>

</template>

<script>
  import './assets/styles/app.styl'
  import BetButton from "./components/Utilities/BetButton";
  import {AUTH_CONFIG} from "./auth_config";
  import OperaComponent from "./components/Pages/OperaComponent";
  import JackpotAdvert from "./components/layout/JackpotAdvert";
  const Sidebar = () => import('./components/layout/Sidebar.vue');
  const Landing = () => import('./components/Landing/Landing.vue');
  const Footer = () => import('./components/layout/Footer.vue');
  const Betslip = () => import('./components/Betslip/Betslip.vue');
  const TopNav = () => import('./components/layout/Topnav.vue');
  const Groupbar = () => import('./components/layout/Groupbar.vue');
  const AccountBar = () => import('./components/layout/AccountBar.vue');

  export default {
    name: 'App',
    components: {
        JackpotAdvert,
        OperaComponent, BetButton, TopNav, Sidebar, Landing, Footer, Betslip, Groupbar, AccountBar},
    data () {
      return {
        isOpen: false,
        isOpera: false
      }
    },
    mounted () {
        this.detectUserBrowser()
    },
    methods: {
      showSlip () {
        this.$emit('openSlip')
      },

      detectUserBrowser () {
          let hasChangedSettings = false;
          if (localStorage.getItem('opera')) {
              hasChangedSettings = true
          }
        if (AUTH_CONFIG.detectUserBrowser(navigator.userAgent.toLocaleLowerCase()) === 'opera') {
            this.isOpera = !hasChangedSettings;
        }
      }

    },

  onActive ()  {
    this.$store.dispatch('refreshUpcomingGames');
      this.$store.dispatch('refreshHighlightedGames');
      this.$store.dispatch('refreshFeaturedGames');
      this.$store.dispatch('refreshSideMenu');
      const authUser = JSON.parse(window.localStorage.getItem('authUser'));
      if (authUser) {
          this.$store.dispatch('init_auth_user', {request: 'refresh'})
      }
  },
  onIdle ()  {
    this.$store.dispatch('refreshUpcomingGames');
      this.$store.dispatch('refreshHighlightedGames');
      this.$store.dispatch('refreshFeaturedGames');
      this.$store.dispatch('refreshSideMenu');
      const authUser = JSON.parse(window.localStorage.getItem('authUser'));
      if (authUser) {
          this.$store.dispatch('init_auth_user', {request: 'refresh'})
      }
  }
  
  }
</script>

