import * as mutationTypes from './mutation_types'

export const mutations = {
  [mutationTypes.SET_AUTH_USER] (state, payload) { state.authUser = payload },
  [mutationTypes.CLEAR_AUTH_USER] (state) { state.authUser = null },
  [mutationTypes.SET_USER_TRANSACTIONS] (state, payload) { state.userTransactions = payload },
  [mutationTypes.SET_USER_BET_HISTORY] (state, payload) { state.betHistory = payload },
  [mutationTypes.SET_USER_DETAILS] (state, payload) { state.userDetails = payload },
  [mutationTypes.SET_USER_GROUPS] (state) {
    let userGroup = state.authUser.group
    let memberOfGroups = state.userDetails.member_of.data
    let groups = []
    if (userGroup) {
      groups.push(userGroup)
    }
    if (memberOfGroups && memberOfGroups.length) {
      memberOfGroups.forEach((member) => {
        groups.push(member)
      })
    }
    state.userGroups = groups
  },
  [mutationTypes.SET_USER_INVITATIONS] (state, payload) { state.invitations = payload },
  [mutationTypes.SET_USER_MAN_TO_MAN_REQUESTS] (state, payload) { state.users_man_to_man_requests = payload }
}
