import * as mutationTypes from './mutation_types'
export const mutations = {
  [mutationTypes.SET_JACKPOT_GAMES] (state, payload) {
    state.jackpotGames = payload;
  },
  [mutationTypes.SET_JACKPOT_SLIP] (state, payload) {
    state.jackpotSlip = payload
  },
    [mutationTypes.CLEAR_JACKPOT_SLIP] (state) {
    state.jackpotSlip = [];
  }
};
